/* ------------ Import Normalize ------------ */
/* ------------ Base style ------------ */
* {
  box-sizing: border-box; }

*::before, *::after {
  box-sizing: inherit; }

html {
  height: 100%;
  overflow-wrap: break-word;
  word-wrap: break-word; }

body {
  font-family: "Clear-Sans", "Arial", sans-serif;
  line-height: 1.43;
  font-size: 14px;
  min-width: 320px;
  color: #292929; }

input, textarea {
  border: #666 1px solid;
  outline: none; }
  input:focus:required:invalid, textarea:focus:required:invalid {
    border-color: red; }
  input:required:valid, textarea:required:valid {
    border-color: green; }

ul, ol {
  padding: 0;
  margin: 0;
  list-style: none; }

p {
  margin: 0; }

h1, h2, h3, h4, h5 {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: normal; }

a {
  text-decoration: none; }
  a:hover {
    text-decoration: none; }

.container {
  min-width: 320px;
  max-width: 1140px;
  padding: 0 30px;
  box-sizing: border-box;
  margin: 0 auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

/* ------------ Footer bottom ------------ */
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.header {
  flex: 0 0 auto; }

.content {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

/* ------------ Adaptive image ------------ */
.img-responsive {
  display: block;
  max-width: 100%;
  height: auto; }
  .img-responsive.img-width {
    width: 100%; }

/* ------------ Adaptive video ------------ */
.video-responsive {
  position: relative;
  padding-bottom: 56.25%;
  /* задаёт высоту контейнера для 16:9 (если 4:3 — поставьте 75%) */
  height: 0;
  overflow: hidden; }
  .video-responsive iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

